.button {
  display: block;
  padding: 14px;
  font-family: $font-button;
  font-style: normal;
  font-size: 18px;
  line-height: math.div(20, 18);
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: $color-button-text;
  background-color: $color-button-background;
  border: 2px solid $color-button-border;
  cursor: pointer;

  &:hover {
    color: $color-button-text-hover;
    background-color: $color-button-background-hover;
  }

  &:focus {
    outline: 2px solid $color-outline;
    outline-offset: 2px;
  }

  &:active {
    color: $color-button-text-active;
    outline: none;
  }
}
